  // form validation

  var inputEmail = $(".contact-us-content .input-email");
  // function to validate the email
  function validateEmail($email) {
    var emailReg = /^([\w-\.]+@([\w-]+\.)+[\w-]{2,4})?$/;
    return emailReg.test($email);
  }

  var emailError = true;
